import { useEffect, useRef, useState } from "react";
import JoditEditor from "jodit-react";
import { RxCrossCircled } from "react-icons/rx";
import { FaCloudUploadAlt } from "react-icons/fa";
import { ToastContainer, toast } from "react-toastify";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import {
  addTicket,
  cloudImage,
  department,
  dmtDisputePriority,
  relatedService,
  userValidate,
} from "../../../../api/login/Login";
import { baseUrlImage, mobileNumberFilter } from "../../../../baseUrl";

function AddTicket({ placeholder }) {
  const navigate = useNavigate();
  const location = useLocation();
  const patam = useParams();
  const editor = useRef(null);

  const [defaultData, setDefaultData] = useState();
  const [departData, setDepartData] = useState([]);
  const [priotyData, setPriotyData] = useState([]);
  const [relateData, setRelateData] = useState([]);
  const [autoFillInitial, setAutoFillInitial] = useState({ name: "", email: "", phone: "" });
  const [initialData, setInitialData] = useState({
    type: "",
    subject: "",
    service_id: "",
    priority: "",
    description: "",
    attachments: [],
  });
  const [listImage, setListImage] = useState([]);

  useEffect(() => setDefaultData(location.state), [location]);

  useEffect(() => {
    depart();
    prioty();
    relatService();
  }, []);

  useEffect(() => {
    setInitialData((prev) => ({ ...prev, service_id: defaultData?.serviceId }));
  }, [defaultData?.serviceId]);

  const handleChange = (e) => {
    setInitialData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const depart = async () => {
    try {
      const res = await department();
      setDepartData(res?.data?.data || []);
    } catch { }
  };
  const prioty = async () => {
    try {
      const res = await dmtDisputePriority();
      setPriotyData(res?.data?.data || []);
    } catch { }
  };
  const relatService = async () => {
    try {
      const res = await relatedService();
      const res2 = await userValidate();
      let resData = res?.data?.data || [];

      if (mobileNumberFilter?.includes(res2?.data?.mobile)) {
        resData = resData.filter((item) => item.service_name === "Courier");
      }
      setRelateData(resData);
      setAutoFillInitial({
        email: res2?.data?.email,
        name: res2?.data?.name,
        phone: res2?.data?.mobile,
      });
    } catch { }
  };

  const colodinaryImage = async (e) => {
    const imgs = new FormData();
    const files = Array.from(e.target.files);

    const allowedTypes = [
      "image/png",
      "image/jpeg",
      "image/jpg",
      "image/gif",
      // "video/mp4",
    ];

    for (let file of files) {
      if (!allowedTypes.includes(file.type)) {
        toast.error("Only images and mp4 videos are allowed!");
        continue;
      }
      try {
        imgs.append("image", file);
        const res = await cloudImage(imgs);
        if (!res?.data?.error) {
          setListImage((prev) => [...prev, { id: Math.random(), url: res?.data?.url }]);
        }
      } catch (err) {
        toast.error("Upload failed!");
      }
    }
  };

  const Remove = (id) => {
    setListImage((prev) => prev.filter((item) => item.id !== id));
  };

  const submitData = async () => {
    if (
      !initialData.subject ||
      !initialData.description || 
      !initialData.priority ||
      !initialData.type ||
      !initialData.service_id
    ) {
      toast.error("Please fill all required fields");
      return;
    }

    const clone = {
      ...initialData,
      description: initialData.description, 
      attachments: listImage.map((i) => i.url),
      user_id: window.localStorage.getItem("userIdToken"),
    };

    try {
      const res = await addTicket(clone);
      if (!res?.data?.error) {
        toast.success(res?.data?.message);
        setTimeout(() => navigate("/list-tickets"), 2000);
      } else {
        toast.error(res?.data?.message);
      }
    } catch {
      toast.error("Something went wrong");
    }
  };



  return (
    <>
      <div className="PageHeading mb-4">
        <h1 className="fw-bold">Add Ticket</h1>
      </div>

      <div className="ContentArea">
        <div className="card shadow-sm rounded-4 p-4">
          <h5 className="mb-4 fw-semibold">Open Ticket</h5>
          <form>
            <div className="row g-3">
              {/* User Info */}
              {["name", "email", "phone"].map((field, idx) => (
                <div className="col-md-4" key={idx}>
                  <label className="form-label text-secondary text-uppercase">
                    {field} <span className="text-danger">*</span>
                  </label>
                  <input
                    type={field === "email" ? "email" : "text"}
                    className="form-control"
                    disabled
                    value={autoFillInitial[field]}
                  />
                </div>
              ))}

              {/* Subject */}
              <div className="col-md-8">
                <label className="form-label text-secondary text-uppercase">
                  Subject <span className="text-danger">*</span>
                </label>
                <input
                  type="text"
                  name="subject"
                  className="form-control bs-autofill-fix"
                  placeholder="Enter Subject"
                  value={initialData.subject}
                  onChange={handleChange}
                />
              </div>

              {/* Select Fields */}
              <div className="col-md-4">
                <label className="form-label text-secondary text-uppercase">Department <span className="text-danger">*</span></label>
                <select
                  className="form-select"
                  name="type"
                  onChange={handleChange}
                  value={initialData.type}
                >
                  <option value="" disabled>Select Type</option>
                  {departData.map((item) => (
                    <option key={item._id} value={item._id}>
                      {item.department}
                    </option>
                  ))}
                </select>
              </div>

              <div className="col-md-4">
                <label className="form-label text-secondary text-uppercase">Related Service <span className="text-danger">*</span></label>
                <select
                  className="form-select"
                  name="service_id"
                  value={initialData.service_id}
                  onChange={handleChange}
                >
                  <option value="" disabled>Select Service</option>
                  {relateData.map((item) => (
                    <option key={item._id} value={item._id}>
                      {item.service_name}
                    </option>
                  ))}
                </select>
              </div>

              <div className="col-md-4">
                <label className="form-label text-secondary text-uppercase">Priority <span className="text-danger">*</span></label>
                <select
                  className="form-select"
                  name="priority"
                  value={initialData.priority}
                  onChange={handleChange}
                >
                  <option value="" disabled>
                    Select Priority
                  </option>
                  {priotyData.map((item) => (
                    <option key={item._id} value={item._id}>
                      {item.priority}
                    </option>
                  ))}
                </select>
              </div>

              {/* Message */}
              <div className="col-12">
                <label className="form-label text-secondary text-uppercase">Message <span className="text-danger">*</span></label>
                <JoditEditor
                  ref={editor}
                  value={initialData.description}
                  onChange={(newContent) => setInitialData({ ...initialData, description: newContent })}
                  className="shadow-sm rounded-3 border"
                />
              </div>

              {/* Upload */}
              <div className="col-md-8">
                <label className="form-label text-secondary text-uppercase">Attachments</label>
                <input
                  type="file"
                  className="form-control mb-2"
                  multiple
                  onChange={colodinaryImage}
                />
              </div>

              <div className="col-md-12 d-flex flex-wrap gap-2">
                {listImage.map((item) => (
                  <div
                    key={item.id}
                    className="position-relative"
                    style={{ width: "100px", height: "100px" }}
                  >
                    <img
                      src={item.url}
                      className="rounded-2 shadow-sm"
                      style={{ width: "100%", height: "100%", objectFit: "cover" }}
                    />

                    <RxCrossCircled
                      onClick={() => Remove(item.id)}
                      className="position-absolute top-0 end-0 text-danger"
                      style={{ cursor: "pointer", fontSize: "1.2rem" }}
                    />
                  </div>
                ))}
              </div>

              <div className="col-md-2 mt-3">
                <button
                  type="button"
                  onClick={submitData}
                  className="btn"
                  style={{ backgroundColor: "#6C757D", color: "#fff" }}
                >
                  Create Ticket
                </button>

              </div>
            </div>
          </form>
        </div>
      </div>

      <ToastContainer position="top-center" autoClose={2000} />
    </>
  );
}

export default AddTicket;
